.img-upload-container {
  margin-bottom: 20px;
  position: relative;
}
.img-upload-container .img-preview-container {
  background-color: #f7f7f7;
}
.img-upload-container .img-preview {
  background: center center no-repeat;
  background-size: contain;
  height: 116px;
  width: 116px;
}
.img-upload-container .img-upload {
  background-color: #61dafb;
  margin-right: 20px;
  margin-bottom: 0;
}
.img-upload-container .upload-text {
  font-size: 17px;
  margin: 0 10px 0 0;
}
.img-upload-container input {
  display: none;
}
.img-upload-container .alert {
  margin-top: 10px;
}
.img-upload-container .img-loading-overlay {
  background-color: black;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  opacity: 0.2;
  position: absolute;
}
.img-upload-container .img-spinning-circle {
  display: inline-block;
  width: 64px;
  height: 64px;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.img-upload-container .img-spinning-circle:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

